<template>
  <div class="home-wrap">
    <div class="tip-wrap">
      提示：您的电动二轮车车牌已生成，请您带上相关证件（身份证、车辆发票、车辆合格证）移步至申请备案社区安装点安装，如不及时安装可能会导致备案标识失效，从而造成您需二次申请，感谢配合。
    </div>

    <div class="card-wrap info-card">
      <img class="title-icon" src="../../../assets/images/title.png" />
      <div class="card-title">
        <div>安装信息</div>
      </div>
      <div class="card-item">
        <div>顺丰快递单号</div>
        <div>{{ orderInfo.waybillNo }}</div>
      </div>
      <div class="card-item last-item">
        <div>申请社区</div>
        <div>{{ orderInfo.area }}</div>
      </div>
    </div>

    <div class="card-wrap date-card" v-if="isEdit">
      <img class="title-icon" src="../../../assets/images/title.png" />
      <div class="card-title">
        <div>选择预约安装时间</div>
      </div>
      <div class="card-item" @click="showDate = true">
        <div>选择日期</div>
        <div>
          <span>{{ orderInfo.planDate || '请选择' }}</span>
          <img class="arrow-icon" src="../../../assets/images/arrow.png" />
        </div>
      </div>
      <div class="card-item last-item" @click="showTime = true">
        <div>选择时间</div>
        <div>
          <span>{{ orderInfo.planTime || '请选择' }}</span>
          <img class="arrow-icon" src="../../../assets/images/arrow.png" />
        </div>
      </div>
    </div>

    <div class="card-wrap date-card" v-if="!isEdit">
      <img class="title-icon" src="../../../assets/images/title.png" />
      <div class="card-title">
        <div>安装时间</div>
        <div class="edit-btn" @click="isEdit = true">
          <span>更改日期</span>
          <img class="arrow-icon" src="../../../assets/images/g_arrow.png" />
        </div>
      </div>
      <div class="card-item">
        <div>日期</div>
        <div>
          <span>{{ orderInfo.planDate }}</span>
        </div>
      </div>
      <div class="card-item last-item">
        <div>时间</div>
        <div>
          <span>{{ orderInfo.planTime }}</span>
        </div>
      </div>
    </div>

    <van-popup v-model="showDate" position="bottom">
      <van-picker
        show-toolbar
        :columns="dateColumns"
        @confirm="onConfirmDate"
        @cancel="showDate = false"
      />
    </van-popup>

    <van-popup v-model="showTime" position="bottom">
      <van-picker
        show-toolbar
        :columns="timeColumns"
        @confirm="onConfirmTime"
        @cancel="showTime = false"
      />
    </van-popup>

    <div class="btn-wrap" v-if="!isBooked">
      <van-button
        color="linear-gradient(to right, #27866D, #94E29B)"
        block
        round
        @click="handleBook"
        >立即预约</van-button
      >
    </div>
    <div class="btn-wrap" v-if="isBooked && isEdit">
      <!-- <van-button
        v-if="isBooked && !isEdit"
        color="#27866D"
        plain
        block
        round
        @click="cancelBook"
        >取消预约</van-button
      > -->
      <van-button
        v-if="isEdit"
        class="left-btn"
        color="#27866D"
        plain
        block
        round
        @click="cancelEdit"
        >取消更改</van-button
      >
      <van-button
        v-if="isEdit"
        color="linear-gradient(to right, #27866D, #94E29B)"
        block
        round
        @click="handleBook"
        >完成更改</van-button
      >
    </div>
  </div>
</template>

<script>
import { Popup, Button, Picker, Toast, Dialog } from 'vant'

export default {
  components: {
    [Popup.name]: Popup,
    [Button.name]: Button,
    [Picker.name]: Picker,
    [Toast.name]: Toast,
    [Dialog.name]: Dialog
  },
  data () {
    return {
      orderInfo: {
        waybillNo: '',
        area: '',
        planDate: '',
        planTime: ''
      },
      dateColumns: [],
      timeColumns: ['09:00-12:00', '13:00-18:00', '19:00-21:00'],
      showDate: false,
      showTime: false,
      isBooked: false,
      isEdit: true
    }
  },
  mounted () {
    this.getWaybillNo()
  },
  methods: {
    getWaybillNo () {
      this.jungleDateRange()
      this.isBooked = false
      const waybillNo = this.getQueryVal()
      this.orderInfo.waybillNo = waybillNo
      if (!waybillNo) {
        return false
      } else {
        this.getOrderInfo()
      }
    },
    getOrderInfo () {
      this.$https
        .get('/api/car/record', {
          params: { waybillNo: this.orderInfo.waybillNo }
        })
        .then(res => {
          if (res && res.data) {
            this.orderInfo = res.data
            const { planDate, currentTime } = res.data
            if (planDate) {
              this.isBooked = true
              this.isEdit = false
            }
            this.jungleDateRange(currentTime)
          }
        })
        .catch(() => {
          this.isEdit = true
          // console.log('err', err)
        })
    },
    jungleDateRange (currentTime) {
      let currentHour = new Date().getHours()
      if (currentTime) {
        currentHour = new Date(currentTime).getHours()
      }
      if (currentHour < 17) {
        this.dateColumns = this.getDateList(1, 2)
      } else {
        this.dateColumns = this.getDateList(2, 1)
      }
    },
    getQueryVal () {
      let val = ''
      const path = window.location.href.split('?')
      if (path && path[1] && path[1].includes('=')) {
        const arr = path[1].split('=')
        if (arr && arr[1]) {
          val = arr[1]
        }
      }
      return val
    },
    onConfirmDate (val) {
      this.orderInfo.planDate = val
      this.showDate = false
    },
    onConfirmTime (val) {
      this.orderInfo.planTime = val
      this.showTime = false
    },
    handleBook () {
      const that = this
      const { waybillNo, planDate, planTime } = this.orderInfo
      if (!waybillNo) {
        Toast.fail('无安装信息，无法提交')
        return false
      }
      if (!planDate) {
        Toast.fail('请选择安装日期')
        return false
      }
      if (!planTime) {
        Toast.fail('请选择安装时间')
        return false
      }
      Dialog.confirm({
        title: '立即预约',
        message: '是否立即预约'
      }).then(() => {
        Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '正在提交预约信息'
        })
        that.$https
          .post('/api/car/record', {
            waybillNo,
            planDate,
            planTime
          })
          .then(res => {
            Toast.clear()
            if (res && res.data) {
              Toast.success('预约成功')
              that.orderInfo = res.data
              that.isEdit = false
              that.isBooked = true
            } else {
              Toast.fail(res + '')
              Toast.clear()
            }
          })
          .catch(err => {
            Toast.fail(err + '')
            Toast.clear()
          })
      })
    },
    cancelBook () {
      const that = this
      const { waybillNo } = this.orderInfo
      Dialog.confirm({
        title: '取消预约',
        message: '是否取消预约'
      }).then(() => {
        Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '正在取消预约'
        })
        that.$https
          .post('/api/car/record/cancel', {
            waybillNo
          })
          .then(res => {
            Toast.success('已成功取消预约')
            if (res && res.data) {
              that.orderInfo = res.data
              that.isEdit = false
              that.isBooked = false
            } else {
              Toast.fail(res + '')
              Toast.clear()
            }
          })
          .catch(err => {
            Toast.fail(err + '')
            Toast.clear()
          })
      })
    },
    cancelEdit () {
      this.isEdit = false
      this.getOrderInfo()
    },
    /**
     * Get date list by range
     * @param {number} dayNo 今天为0，明天天为1，后天为2...以此类推
     * @param {number} range 范围天数
     * @returns {array} dateList
     */
    getDateList (dayNo, range) {
      let dateList = []
      if (dayNo >= 0 && range) {
        dateList = this.getDateListByRange(dayNo, range)
      }
      return dateList
    },
    parseDate (time) {
      const date = new Date(time)
      const year = date.getFullYear()
      const month =
        date.getMonth() + 1 < 10
          ? '0' + (date.getMonth() + 1)
          : date.getMonth() + 1
      const day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
      return year + '-' + month + '-' + day
    },
    getDateListByRange (dayNo, range) {
      const dateList = []
      const now = new Date().getTime()
      for (let i = dayNo; i < range + dayNo; i++) {
        dateList.push(this.parseDate(now + 3600 * 1000 * 24 * i))
      }
      return dateList
    }
  }
}
</script>

<style lang="less">
.home-wrap {
  width: 100vw;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(../../../assets/images/bg.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding-bottom: 114px;
}

.tip-wrap {
  margin-top: 225px;
  box-sizing: border-box;
  width: calc(100% - 30px);
  text-align: justify;
  color: #2e8c70;
  font-size: 12px;
  line-height: 20px;
}

.card-wrap {
  box-sizing: border-box;
  position: relative;
  width: calc(100% - 30px);
  padding: 30px 15px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0 0 25px 0 #a0dbaf;
  border-radius: 10px;
  margin-top: 15px;
}

.title-icon {
  position: absolute;
  top: 33px;
  left: 0;
  width: 3px;
  height: 20px;
}

.card-title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  font-size: 17px;
  font-weight: 600;
  color: #323232;
}

.edit-btn {
  display: flex;
  align-items: center;
  color: #28876d;
  font-weight: 400;
  font-size: 15px;
}

.card-item {
  display: flex;
  justify-content: space-between;
  font-size: 15px;
  padding: 15px 0;
}

.date-card {
  margin-bottom: 18px;
}

.card-item div:first-child {
  color: #323232;
}

.card-item div:last-child {
  position: relative;
  display: flex;
  align-items: center;
  color: #909399;
}

.last-item {
  border-top: 1px solid #ebeef5;
  padding-bottom: 0;
}

.arrow-icon {
  margin-left: 10px;
  width: 5px;
  height: 10px;
}

.btn-wrap {
  display: flex;
  justify-content: space-between;
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  width: 100%;
  padding: 15px;
  background: #ffffff;
}

.left-btn {
  margin-right: 25px;
}
</style>
